import { g2Factory, getConsoleConfig, Utils, dataAdapter, Constants, g2Tooltip, g2Legend, g2Guide, g2Label, g2Style, g2Size } from '@alicloud/console-shared-utils';

var COMPONENT_NAME = 'ConsolePieChart';
function transformCoord(coord, transform) {
    if (transform === void 0) { transform = {}; }
    var type = transform.type, param = transform.param;
    if (coord[type] && Array.isArray(param)) {
        coord[type].apply(coord, param);
    }
}
function selectGeom(geom, selectKey) {
    if (!geom) {
        return;
    }
    // 清除选中效果
    geom.clearSelected();
    // 如果selectKey为假值，则只清空选中效果。
    if (!selectKey) {
        return;
    }
    // 使用内部方法直接选中，fix: 数据同时被更新时无法选中的问题
    geom.getShapes().forEach(function (shape) {
        var origin = shape.get('origin');
        if (Utils.get(origin, '_origin.x') === selectKey) {
            geom.setShapeSelected(shape);
        }
    });
}
var cfg = {
    getUnifiedProps: function (props) {
        var config = props.config;
        var defaultConfig = getConsoleConfig()[COMPONENT_NAME];
        var newConfig = Utils.merge({}, defaultConfig, config);
        var unionProps = Object.assign({}, props, {
            padding: props.padding || config.padding,
            config: newConfig,
        });
        return unionProps;
    },
    changeSize: function (chart, config, w, h) {
        chart.changeSize(w, h);
        selectGeom(this.geom, config.selectData);
    },
    changeCustomConfig: function (objValue, othValue, key) {
        if (key === 'selectData' && objValue !== othValue) {
            selectGeom(this.geom, objValue);
            return true;
        }
    },
    drawChart: function (chart, config, data) {
        var defs = {
            type: {
                type: 'cat',
            },
        };
        var adaptData = dataAdapter(data, config);
        if (config.autoSort) {
            adaptData.sort(function (a, b) { return b.y - a.y; });
        }
        chart.source(adaptData, defs);
        // 重要：绘制饼图时，必须声明 theta 坐标系
        var thetaConfig = {
            radius: 1,
        };
        if (config.cycle) {
            var innerRadius = config.innerRadius || 0.75;
            thetaConfig.innerRadius = Math.min(innerRadius, 1);
        }
        var coord = chart.coord('theta', thetaConfig);
        if (config.coord) {
            var transform = config.coord && config.coord.transform;
            if (Array.isArray(transform)) {
                transform.forEach(function (t) {
                    transformCoord(coord, t);
                });
            }
            else if (transform && typeof transform === 'object') {
                transformCoord(coord, transform);
            }
        }
        // 计算得总数据
        var totalData = 0;
        adaptData.forEach(function (d) {
            totalData += d.y;
        });
        // position若直接使用value导致图例点击某项隐藏，余下展示不为值和不为1
        var colors = config.colors || Utils.get(this, 'consoleTheme.colors_24') || Constants.COLORS_24;
        this.geom = chart
            .intervalStack()
            .position('y')
            .color('x', colors)
            .select(config.select);
        g2Tooltip(chart, config, config.tooltip, { showTitle: false });
        g2Legend(chart, config, config.legend);
        g2Guide(chart, config, config.guide);
        g2Label(this.geom, config, config.label, 'y', 'y', { total: totalData });
        g2Style(this.geom, config, config.style);
        g2Size(this.geom, config, config.size, 'x');
        chart.render();
    },
    afterRender: function (chart, config) {
        var _this = this;
        // 这里主要是为了实现 selectData 特性，保证在1s内只调用一次selectGeom，并且是在节流结束之后触发, 保证最后一次的正确显示
        if (this.throttleSelect) {
            this.throttleSelect(config);
        }
        else {
            this.throttleSelect = Utils.throttle(function (config) {
                selectGeom(_this.geom, config.selectData);
            }, 1000, { trailing: true });
        }
    },
    destroy: function () {
        this.geom = null;
    },
};
var ConsolePieChart = g2Factory(COMPONENT_NAME, cfg);

export default ConsolePieChart;
